<style lang="scss">
    @import "./css/style.scss";
</style>
<template>
    <div id="index">
        <ToolBar>
            <div></div>
            <div>
                <SelectTags v-model="searchParams.labelList"/>
                <el-input
                    placeholder="请输入原子课名称或原子课ID"
                    size="small"
                    style="width: 220px"
                    v-model="searchParams.keyWord"
                    clearable
                ></el-input>
                <el-select
                    v-model="searchParams.sellType"
                    size="small"
                    clearable
                    placeholder="售卖类型"
                    style="width: 140px"
                >
                    <el-option
                        v-for="(k,v) in $Cfg.mySales"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-select
                    v-model="searchParams.limtFree"
                    size="small"
                    clearable
                    placeholder="限时免费"
                    style="width: 140px"
                >
                    <el-option
                        v-for="(v,k) in $Cfg.freeLimitedTime"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-select
                    v-model="searchParams.discountType"
                    size="small"
                    clearable
                    placeholder="折扣类型"
                    style="width: 140px"
                >
                    <el-option
                        v-for="(v,k) in $Cfg.discountTypes"
                        :key="k"
                        :label="v"
                        :value="k"></el-option>
                </el-select>
                <Operator v-model="searchParams.operator"/>
                <el-button type="success" size="small" @click="refresh = !refresh">查询</el-button>
                <el-button type="warning" size="small" @click="resetFn">重置</el-button>
            </div>
        </ToolBar>
        <el-table :data="tableData" border style="width: 100%">
            <el-table-column prop="id" label="ID" width="180">100861000010001</el-table-column>
            <el-table-column prop="name" label="原子课名称"></el-table-column>
            <el-table-column prop="label" label="运营标签"></el-table-column>
            <el-table-column prop="address" label="售卖类型"></el-table-column>
            <el-table-column prop="free" label="限时免费">是</el-table-column>
            <el-table-column prop="money" label="售卖金额">30000.00</el-table-column>
            <el-table-column prop="discount" label="折扣类型"></el-table-column>
            <el-table-column prop="paperNumber" label="试题数量">100</el-table-column>
            <el-table-column prop="operator" label="最近操作人">张三</el-table-column>
            <el-table-column prop="date" label="发布时间">2019-3-5 14:50</el-table-column>
            <el-table-column prop="address" label="状态">启用</el-table-column>
            <el-table-column label="操作" width="210">
                <template slot-scope="scope">
                    <el-button v-if="scope.row.isopen" @click="forbidden(scope.row)" type="danger" size="small">禁用
                    </el-button>
                    <el-button v-else @click="enable(scope.row)" type="success" size="small">启用</el-button>
                    <el-button @click="todetail(scope.row)" type="info" size="small">预览</el-button>
                    <el-button @click="editorFn(scope.row)" type="primary" size="small">修改</el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination :params="searchParams" :requestFunc="requestFunc" @returnData="returnData"/>
    </div>
</template>

<script>
    import ToolBar from "@/components/ToolBar.vue";
    import HelpHint from "@/components/HelpHint.vue";
    import Pagination from "@/components/Pagination.vue";
    import paginationMixin from "./mixin/page.js";
    import SelectTags from '@/components/SelectTags/SelectTags.vue';
    import { resetArgs } from '@/utils/index.js'

    export default {
        mixins: [paginationMixin],
        data() {
            return {
                tableData: [
                    {
                        ID: '1234567891001',
                        date: "2016-05-03",
                        name: "Lorem ipsum dolor sit amet,",
                        isopen: true,
                        id: '123',
                        address:
                            "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde."
                    },
                    {
                        ID: '1234567891001',
                        date: "2016-05-02",
                        name: "Lorem ipsum dolor sit amet,",
                        isopen: false,
                        id: '456',
                        address:
                            "Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde."
                    }
                ]
            };
        },
        methods: {
            resetFn() {
                resetArgs(this.searchParams)
            },
            todetail(row) {
                this.$router.push({name: 'AtomCourseDetails', params: {id: row.id}})
            },
            editorFn(row) {
                this.$router.push({name: 'AtomCourseEditor', params: {id: row.id}})
            },
            forbidden(row) {
                this.$confirm('确定禁用该原子课?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '禁用成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消禁用'
                    });
                });
            },
            enable(row) {
                this.$confirm('确定启用该原子课?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'success'
                }).then(() => {
                    this.$message({
                        type: 'success',
                        message: '启用成功!'
                    });
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消启用'
                    });
                });
            }
        },
        created() {
        },
        components: {
            ToolBar,
            HelpHint,
            Pagination,
            SelectTags,
        }
    };
</script>
